import React, { useState } from 'react'
import type { Story } from '@storybook/react/types-6-0'
import { ExtendComponentsThemeProvider } from '@looker/components-providers'
import { defaultArgTypes as argTypes } from '@looker/storybook'
import { ButtonToggle } from '../../../Button'
import { Fieldset } from '../../Fieldset'
import type { FieldTimeSelectProps } from './'
import { FieldTimeSelect } from './'

export default {
  argTypes,
  component: FieldTimeSelect,
  title: 'Date / FieldTimeSelect',
}

const Template: Story<FieldTimeSelectProps & { externalLabel: boolean }> = ({
  externalLabel = true,
  ...args
}) => (
  <ExtendComponentsThemeProvider
    themeCustomizations={{ defaults: { externalLabel } }}
  >
    <FieldTimeSelect {...args} />
  </ExtendComponentsThemeProvider>
)

export const Basic = Template.bind({})
Basic.args = {
  defaultValue: '14:30',
  interval: 10,
  label: 'Select Time',
}

export const FloatingLabel = Template.bind({})
FloatingLabel.args = {
  ...Basic.args,
  externalLabel: false,
}

export const Disabled = Template.bind({})
Disabled.args = {
  disabled: true,
  interval: 10,
  label: 'Select Time',
}

export const Required = Template.bind({})
Required.args = {
  interval: 10,
  label: 'Select Time',
  required: true,
}

export const Error = Template.bind({})
Error.args = {
  description: 'this is the description is a very long one',
  detail: 'detail',
  interval: 10,
  label: 'Select Time',
  required: true,
  validationMessage: { message: 'validation Message', type: 'error' },
}

export const ErrorFloatingLabel = Template.bind({})
ErrorFloatingLabel.args = {
  ...Error.args,
  externalLabel: false,
}

export const Controlled = () => {
  const [controlledTime, setControlledTime] = useState<any>('09:00')
  const handleClick = (value: string) => setControlledTime(value)

  const options = [
    { label: '11:05a', value: '11:05' },
    { label: '2:00pm', value: '14:00' },
    { label: '3:15pm', value: '15:15' },
    { label: '4:30pm', value: '16:30' },
  ]

  return (
    <>
      <ButtonToggle
        value={controlledTime}
        onChange={handleClick}
        options={options}
      />

      <Fieldset inline>
        <FieldTimeSelect
          label="Standard Time"
          value={controlledTime}
          onChange={setControlledTime}
          autoFocus
        />
        <FieldTimeSelect
          label="Military Time"
          value={controlledTime}
          onChange={setControlledTime}
          format="24h"
        />
      </Fieldset>
    </>
  )
}

Controlled.parameters = {
  storyshots: { disable: true },
}
